body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  margin-left: 30px;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.error {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #d9534f;
  border-radius: 5px;
  background-color: #f2dede;
  color: #a94442;
  font-size: 16px;
  font-family: Arial, sans-serif;
}

/* Убираем плавное перемещение */
.part {
    position: absolute;
    transition: none; /* Убираем любые переходы */
}


/* Переход для перемещения */
.part-moving {
    transition: none; /* Отключаем плавный переход, пока деталь перетаскивается */
}

.parallax-background {
  background-image: url('C:\Users\User\cutting-calculator\img\abstract.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  filter: blur(8px);
  background-color: rgba(0, 0, 0, 0.3); /* Optional: Add a dark overlay for better text readability */
}

.background-gradient {
  background: linear-gradient(to bottom right, #f5f7fa, #c3cfe2);
  background-attachment: fixed;
}
.svg-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.svg-detail {
  vector-effect: non-scaling-stroke;
}

.border.p-6.bg-gray-100.h-\[400px\].relative.shadow-md {
    margin-top: 90px;
}


@media (min-width: 320px) and (max-width: 768px) {
	.border.p-6.bg-gray-100.h-\[400px\].relative.shadow-md {
		max-width: 230px;
		max-height: 100px;

}}

@media (min-width: 769px) and (max-width: 992px) {
	.border.p-6.bg-gray-100.h-\[400px\].relative.shadow-md {
		max-width: 300px;
		max-height: 100px;

}}


@media (min-width: 993px) and (max-width: 1024px) {
	.border.p-6.bg-gray-100.h-\[400px\].relative.shadow-md {
		max-width: 380px;
		max-height: 120px;

}}

@media (min-width: 1025px) and (max-width: 1279px) {
	.border.p-6.bg-gray-100.h-\[400px\].relative.shadow-md {
		max-width: 550px;
